import { baseUrl } from "../../common/api";
export default {
	data() {
		return {
			title: 'Hello',
			bannerList: [],
			swiperTitle: '',
			swiperColor: '',
			recommendList: [],
			dishFlag: true,
			albumsList:[],
			newsongList: [],
			navigateFlag: false,
		}
	},
	onLoad() {
		uni.request({
			url: baseUrl+"/banner",
			data:{
				type: 1
			},
			success: (res) => {
				let { data } = res;
				if (data.code == 200) {
					// console.log(data.banners)
					this.bannerList = data.banners;
					this.swiperTitle = data.banners[0].typeTitle;
					this.swiperColor = data.banners[0].titleColor;
				}
			},
			fail: (err) => {
				// console.log(err)
			},
			complete: (res) => {
				// console.log(res)
			}
		})
		this.recommendSel()
		this.albumNew()
		this.songNew()
	},
	methods: {
		transformNum(val) {
			let value = val.toString().length
			switch (true){
				case value<=5:
					return val
					break;
				case value==6:
					return val.toString().substr(0,2)+"万"
					break;
				case value==7:
					return val.toString().substr(0,3)+"万"
					break;
				case value==8:
					return val.toString().substr(0,4)+"万"
					break;
				case value==9:
					return val.toString().substr(0,1)+"."+val.toString().substr(1,1)+"亿"
					break;
				case value==10:
					return val.toString().substr(0,2)+"亿"
					break;
				default:
					break;
			}
		},
		
		/* banner切换监听 */
		swiperChange(e) {
			let index = e.detail.current;
			this.swiperTitle = this.bannerList[index].typeTitle;
			this.swiperColor = this.bannerList[index].titleColor;
		},
		
		/* 推荐歌单请求 */
		recommendSel() {
			uni.request({
				url: baseUrl+"/personalized",
				data:{
					limit: 6
				},
				success: (res) => {
					let { data } = res;
					if (data.code == 200) {
						this.recommendList = data.result;
					}
				},
				fail: (err) => {
					// console.log(err)
				},
				complete: (res) => {
					// console.log(res)
				}
			})
		},
		
		/* 新碟/新歌切换 */
		dishChange() {
			this.dishFlag = !this.dishFlag;
		},
		
		/* 获取最新专辑 */
		albumNew() {
			uni.request({
				url: baseUrl+"/album/newest",
				data:{},
				success: (res) => {
					let { data } = res;
					if (data.code == 200) {
						this.albumsList = data.albums
						// console.log(data.albums)
					}
				},
				fail: (err) => {
					// console.log(err)
				},
				complete: (res) => {
					// console.log(res)
				}
			})
		},
		
		/* 获取新歌 */
		songNew() {
			uni.request({
				url: baseUrl+"/personalized/newsong",
				data:{},
				success: (res) => {
					let { data } = res;
					if (data.code == 200) {
						data.result.map((i, index) => {
							i.authors = "";
							i.song.artists.forEach((j ,idx) => {
								i.authors += j.name+ '/';
							})
							i.authors = i.authors.substr(0,i.authors.length - 1)
						})
						this.newsongList = data.result
						// console.log(data.result)
					}
				},
				fail: (err) => {
					// console.log(err)
				},
				complete: (res) => {
					// console.log(res)
				}
			})
		},
		
		/* gotoPage */
		goDetailPage(path) {
			if (this.navigateFlag) {
				return;
			}
			this.navigateFlag = true;
			uni.navigateTo({
				url: '/pages/cloud-music/' + path + '/' + path
			});
			setTimeout(() => {
				this.navigateFlag = false;
			}, 200)
		}
	}
}
